.home-page {
  width: 600px;
  position: fixed;
  top: 30%;
  left: 35%;
}
.home-page .bars {
  font-size: 45px;
  position: fixed;
  top: 40px;
  left: 40px;
  color: black;
  cursor: pointer;
}
.home-page .login-form {
  text-align: center;
  opacity: 0.7;
}
.home-page img {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -100;
  -webkit-filter: blur(2px);
          filter: blur(2px);
}
html,
body {
  width: 100%;
  height: 100%;
}
.hanwen {
  position: fixed;
  top: 50px;
  right: 50px;
  color: black;
  font-weight: 700;
  opacity: 0.3;
  font-size: 30px;
}
.formLogin {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}
.formLogin .forma {
  margin-left: 20px;
  color: black;
  font-size: 18px;
  font-weight: 700;
}
.ant-table table {
  width: 100%;
  text-align: left;
  border-radius: 4px 4px 0 0;
  border-collapse: collapse;
  table-layout: fixed;
}
.ant-spin-container {
  position: relative;
  transition: opacity 0.3s;
  -webkit-line-clamp: 3;
  box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
  cursor: pointer;
}
.itemMeta {
  display: block;
  width: 100%;
}
.ant-list-item-meta {
  display: -webkit-flex;
  display: flex;
  -webkit-flex: 1 1;
          flex: 1 1;
  -webkit-align-items: center;
          align-items: center;
  font-size: 0;
}
.loadmore-list {
  min-height: 800px;
}
.user-list {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  overflow: scroll;
}
.homemenu {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  width: 75%;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: baseline;
          align-items: baseline;
  font-size: 2em;
  margin-top: 30px;
}
.homemenu a {
  color: black;
  font-weight: 700;
  margin-left: 50px;
}
.homemenu a:hover {
  font-size: 30px;
  color: #ffc0cb;
}
.showarticle {
  position: absolute;
  width: 100%;
  background: rgba(0, 0, 0, 0.6);
  z-index: 100;
}
